import React, { PureComponent } from 'react';
import { instanceOf } from 'prop-types';
import { withCookies, Cookies } from 'react-cookie';
import { APPHOST } from '../../../actions/api';
import { CloudHostUrl } from '../../../constants';
import './upgrade-popup-style.css'

export const VERIFY_IDS = [26032, 27439, 27454, 26911, 390, 25877]
const CloudUrl = `${CloudHostUrl}app/theme/display`;

class TaggboxBranding extends PureComponent {
    static propTypes = {
        cookies: instanceOf(Cookies).isRequired
    };
    onCreateCookies = event => {
        const { cookies } = this.props;
        var date = new Date();
        date.setTime(date.getTime() + (1 * 60 * 60 * 1000));
        cookies.set('planUpgrade', date, { path: '/', expires: date });
    }
    render() {
        const { showBranding, wall, dismissPopUp } = this.props;
        return (<React.Fragment>
            <div className={`tb_upgrade_001 ${showBranding ? 'tb_show_02_' : ''}`}>
                <div className="tb_popup_wrap_002">
                    <div className="tb_popup_wrap_in__">
                        <div className="tb_popup_body_">
                            <div onClick={dismissPopUp} className="tb_close_btn00">
                                <img src={`${CloudUrl}/images/remove-icon-small.svg`} />
                            </div>
                            <div className="tb_pupup_content__003">
                                <div className="tb_popup_img__branding" style={{ backgroundImage: `url(${CloudUrl}/images/Upgrade-popup-free-tial-poweredBy.svg)` }}>
                                    <div className="tb_title_004">Remove Branding</div>
                                </div>
                                <div className="tb_pera__004">
                                    Wish to upgrade your plan to remove Taggbox's branding?
                                </div>
                                <div className="tb_popup_btn_004">
                                    {
                                        wall != null && parseInt(wall.Wall.owner) && VERIFY_IDS.includes(wall.Wall.owner) ?
                                            <a target="_blank" className="tb_btn__005" href={`${APPHOST}events`} onClick={this.onCreateCookies}>Upgrade Now!</a> :
                                            <a target="_blank" className="tb_btn__005" href="https://taggbox.com/price/display-price/">Upgrade Now!</a>
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {(showBranding ? <div className={`tb_pop_overlay__  tb_show_02_`}></div> : '')}

            <div className={`taggbox_branding ${showBranding ? 'move__' : ''}`} style={{ display: wall.UserRule.branding ? '' : 'none' }}>
                <div className="taggbox_branding_00" data-toggle="modal" data-target="#taggboxBranding_div_modal" onClick={dismissPopUp}>
                    <img src={`${CloudUrl}/images/PoweredBy-taggbox.svg`} alt="Taggbox" ></img>
                </div>
            </div>

        </React.Fragment>)
    }
}

export default withCookies(TaggboxBranding);